.ng2-c-popup {
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0 8px 5px 0 rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  color: #28648a;
  -webkit-overflow-scrolling: touch;
  position: fixed;
  /*
     * hide the popup by default and then make it visible from code
     * why: popup flickers on iOS
     * why: there is a small delay between creating the popup's embedded view and
     *      setting its position using an ngStyle binding
     * in Chrome/Android, the delay is unnoticable
     * in Safari, the popup is rendered with left: 0; top: 0; before ngStyle is applied
     */
  visibility: hidden;
  width: 300px; }

.ng2-c-popup--visible {
  visibility: visible; }

.ng2-c-popup--center {
  display: flex;
  justify-content: center;
  text-align: center; }

.ng2-c-popup--center .ng2-c-popup__body {
  width: 100%; }

.ng2-c-popup__body {
  border-radius: 4px;
  box-sizing: border-box;
  max-height: inherit;
  overflow: hidden;
  overflow-y: auto; }

.ng2-c-popup__arrow {
  border-left: 13px solid transparent;
  border-right: 13px solid transparent;
  content: "";
  height: 0;
  left: 50%;
  pointer-events: none;
  position: absolute;
  width: 0; }

.ng2-c-popup__arrow--down {
  border-top: 11px solid #ffffff;
  top: 100%;
  transform: translate(-50%, -1px); }

.ng2-c-popup__arrow--up {
  border-bottom: 11px solid #ffffff;
  bottom: 100%;
  transform: translate(-50%, 1px); }

.ng2-c-popup__body--has-padding {
  padding: 15px; }

.ng2-c-popup__item {
  border-bottom: 1px solid #f5f5f5;
  /* Assume that items are always used as buttons */
  cursor: pointer;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  min-height: 30px;
  overflow: hidden;
  padding: 10px;
  text-overflow: ellipsis;
  white-space: nowrap; }
  .ng2-c-popup__item:last-child {
    border-bottom: none; }

.ng2-c-popup__item--no-padding {
  padding: 0; }

.ng2-c-popup__buttons {
  border-top: 1px solid #f5f5f5;
  display: flex; }

.ng2-c-popup__button {
  flex-grow: 1;
  padding: 10px;
  padding-bottom: 15px;
  text-align: center;
  width: 50%; }

.ng2-c-popup__title {
  color: #ffffff;
  margin-bottom: 15px; }

.ng2-c-popup__text {
  margin-bottom: 20px; }

.ng2-c-popup__close-button {
  text-decoration: underline; }

.ng2-c-popup--coachmark {
  background-color: #6ec60f;
  box-shadow: 0 8px 5px 0 rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  color: #ffffff;
  padding: 15px; }
  .ng2-c-popup--coachmark .ng2-c-popup__body {
    margin-bottom: 15px;
    margin-top: 15px;
    text-align: center; }
  .ng2-c-popup--coachmark .ng2-c-popup__arrow--up {
    border-bottom: 11px solid #6ec60f; }
  .ng2-c-popup--coachmark .ng2-c-popup__arrow--down {
    border-top: 11px solid #6ec60f; }
